.project{
	background-color: $color-secondary;
	
	&__item{
		max-width: 1200px;
		margin: 0 auto 40px;

		&:first-child{
			margin-top: 40px;
		}

		&__container{
			@include laptop{
				display: flex;
				column-gap: 30px;
				position: relative;

				&:after{
					position: absolute;
					content: " "; 
					width: calc(100% + 30px);
					height: 1px;
					left: -15px;
					bottom: 0;
					background-color: $color-quaternary;
				}
			}
		}

		&__title{
			position: relative;
			font-size: 24px;
			display: flex;
			align-items: center;
			line-height: 1;
			color: $color-tertiary;

			@include tablet{
				font-size: 40px;
			}

			@extend .font-poppins, .font-light;

			&__number{
				font-size: 60px;
				font-style: italic;
				-webkit-text-stroke: 2px $color-tertiary;
				color: $color-primary;
				letter-spacing: -0.2rem;
				margin-right: 10px;

				@extend .font-sourceSerifPro, .font-semibold;

				@include tablet{
					font-size: 70px;
					padding: 0 10 0 0;
				}

				@include laptop{
					font-size: 90px;
					padding: 0 30px;
				}
			}

			&::after, &::before{
				position: absolute;
				content: " "; 
				width: calc(100% + 30px);
				height: 1px;
				left: -15px;
				background-color: $color-quaternary;
			}

			&::before{
				top: 2px;
			}

			&::after{
				bottom: 2px;
			}
		}

		&__img{
			width: 100%;
			max-width: 400px;
			aspect-ratio: 1/1;
			margin: 20px auto;

			@include tablet{
				min-width: 400px;
			}

			img{
				max-width: 100%;
				max-height: 100%;
			}
		}

		&__informations{
			@include laptop{
				position: relative;
				flex-grow: 1;
				&::after{
					position: absolute;
					content: " "; 
					width: 1px;
					height: 100%;
					top: 0;
					left: -15px;
					background-color: $color-quaternary;
				}
			}
		}

		&__details{
			display: flex;
			position: relative;

			&::after, &::before{
				position: absolute;
				content: " "; 
				width: calc(100% + 30px);
				height: 1px;
				left: -15px;
				background-color: $color-quaternary;
			}

			&::after{
				top: 0;

				@include laptop{
					content: none;
				}
			}

			&::before{
				bottom: 0;
			}
			
			div:first-of-type{
				min-width: 30%;
				position: relative;

				&::after{
					position: absolute;
					content: " "; 
					width: 1px;
					height: 100%;
					top: 0;
					right: 0;
					background-color: $color-quaternary;
				}
			}

			&__title{
				text-transform: uppercase;
			}

			&__text{
				@extend .font-sourceSerifPro, .font-semibold;
			}

			p{
				padding: 0 15px;
			}
		}

		&__description{
			&__title{
				font-style: italic;
				font-size: 18px;

				@extend .font-sourceSerifPro, .font-semibold;
			}
		}
	}

	.btnGroup{
		margin: 50px auto 10px;
		display: flex;
		column-gap: 30px;
		flex-wrap: wrap;

		a{
			margin-bottom: 20px;
		}
	}
}